import React, { useState, useEffect } from "react";
import { createClient } from "contentful";

const GetVersions = ({ onVersionsFetched }) => {
  useEffect(() => {
    const client = createClient({
      space: "gush6s9fs1up",
      accessToken: "w7YEK76BONLbrcqWGhFONNUVbmpZIHQmWUrWL2BaXoc",
    });

    client
      .getEntries({
        content_type: "versionUpdates",
      })
      .then((response) => {
        console.log("Raw Response from Contentful:", response); // Log the raw response
        const firstItemFields = response.items[0].fields;        onVersionsFetched({
          iosVersion: firstItemFields.iosVersion || "Default iOS Version",
          dfmVersion: firstItemFields.dfmVersion || "Default DFM Version",
          // dmmVersion: fields.dmmVersion
          //   ? fields.dmmVersion["en-US"]
          //   : "Default DMM Version",
          // dfcVersion: fields.dfcVersion
          //   ? fields.dfcVersion["en-US"]
          //   : "Default DFC Version",
        });
      })
      .catch((error) => console.log(error));
  }, [onVersionsFetched]);

  return null; // This component doesn't render anything itself
};

export default GetVersions;
